.text-style {
  color: #2e3438;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 16px;
  font-weight: 400;
}
.text-style h1,
.text-style h2,
.text-style h3,
.text-style h4,
.text-style h5,
.text-style h6,
.text-style p {
  margin-top: 30px;
  margin-bottom: 30px;
}
.text-style ul {
  list-style: none;
  margin-left: 0px;
  margin-bottom: 20px;
  margin-top: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
}
.text-style ul li {
  position: relative;
  margin-bottom: 12px;
  margin-top: 12px;
  padding-left: 36px;
}
.text-style ul li.with-square::before {
  background-color: #f7b500;
}
.text-style ul li.with-square::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 13px;
  height: 10px;
  background-color: #2e3438;
  transform: translateY(-50%) skew(-15deg);
}
.text-style ul li.orange::before {
  background-color: #f7b500;
}
.text-style ul li.black::before {
  background-color: #2e3438;
}
.text-style ul li.lightblue::before {
  background-color: #00acc1;
}
.text-style ul li:before {
  content: "";
  position: absolute;
  top: 0;
  left: 3px;
